import React from 'react';
import { Container } from 'react-bootstrap';

const Footer = () => {
  return (
    <footer className="footer navbar-static-bottom">
      <Container>
        <p className="footer__text">
           {new Date().getFullYear()} - {' Yuki Nishidate'}
        </p>
      </Container>
    </footer>
  );
};

export default Footer;
