import React, { useState, useEffect } from 'react';
import Fade from 'react-reveal/Fade';
import { Container, Row, Col } from 'react-bootstrap';
import Title from './Title';
import AboutImg from './AboutImg';
import SocialLink from './SocialLink';

const About = () => {
  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="about">
      <Container className="about-container">
        {isDesktop && (
          <Row className="about-wrapper">
            <Col md={6} sm={12}>
              <Fade bottom duration={1000} delay={300} distance="60px">
                <div className="about-wrapper__image">
                  <AboutImg alt="profile picture" filename="logo.png" />
                </div>
              </Fade>
            </Col>
            <Col md={6} sm={12}>
              <Fade left={isDesktop} bottom={isMobile} duration={1000} delay={500} distance="60px">
                <div className="about-wrapper__info">
                  <Title title="Yuki Nishidate" />
                  <p className="about-wrapper__info-text">
                    {
                      '  '
                    }
                  </p>
                  <div className="social-links">
                    <SocialLink href="https://twitter.com/asta18425" name="twitter" />
                    <SocialLink href="https://github.com/nishidate-yuki" name="github" />
                  </div>
                </div>
              </Fade>
            </Col>
          </Row>
        )}
        {isMobile && (
          <Fade left={isDesktop} bottom={isMobile} duration={500} delay={500} distance="60px">
            <div className="about-wrapper__info">
              <Title title="Yuki Nishidate" />
              <p className="about-wrapper__info-text">
                {
                  '  '
                }
              </p>
              <div className="social-links">
                <SocialLink href="https://twitter.com/asta18425" name="twitter" />
                <SocialLink href="https://github.com/nishidate-yuki" name="github" />
              </div>
            </div>
          </Fade>
        )}
      </Container>
    </section>
  );
};

export default About;
