import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import PropTypes from 'prop-types';
import Img from 'gatsby-image';

const ThumbImg = ({ id, isMobile }) => (
  <StaticQuery
    query={graphql`
      query {
        allContentfulAsset {
          nodes {
            id
            square: fixed(height: 240, width: 240) {
              ...GatsbyContentfulFixed
            }
            rect: fluid(maxWidth: 400, maxHeight: 300) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    `}
    render={(data) => {
      const node = data.allContentfulAsset.nodes.find((n) => n.id == id);
      if (!node) return null;

      const style = { boxShadow: '0 0 4px 1px rgba(0, 0, 0, 0.2)' };
      if (isMobile) {
        return <Img fluid={node.rect} style={style} />;
      } else {
        return <Img fixed={node.square} style={style} />;
      }
    }}
  />
);

ThumbImg.propTypes = {
  filename: PropTypes.string,
  alt: PropTypes.string,
  isMobile: PropTypes.bool,
};

export default ThumbImg;
