import React, { useContext, useEffect, useState } from 'react';
import Modal from 'react-modal';
import { Row, Col } from 'react-bootstrap';
import PostDetail from './PostDetail';
import ThumbImg from './ThumbImg';

const Post = ({ node }) => {
  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const [modalIsOpen, setIsOpen] = useState(false);
  const { title, images, description, date, videos, url } = node;

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  const modalStyle = {
    overlay: {
      position: 'fixed',
      top: 0,
      left: 0,
      backgroundColor: 'rgba(0,0,0,0.5)',
    },
    content: {
      position: 'absolute',
      backgroundColor: 'rgba(255, 255, 255, 1)',
      padding: isDesktop ? '15rem' : '2rem',
      maxWidth: '900px',
      margin: 'auto',
      overflowY: 'scroll',
    },
  };

  return (
    <div className="post-link-button-wrapper">
      {/*  */}
      {isMobile && (
        <a className="post-link-button" href={'/post/' + node.id + '/'}>
          <div className="project-wrapper__post">
            <h2 className="cta-btn color-text">{node.title}</h2>
            <div className="post-image">
              <ThumbImg id={node.images[0].id} isMobile={true} />
            </div>
          </div>
        </a>
      )}
      {/*  */}
      {isDesktop && (
        <>
          <button className="post-link-button" onClick={() => setIsOpen(true)}>
            <div className="project-wrapper__post">
              <div className="post-image">
                <ThumbImg id={node.images[0].id} isMobile={false} />
              </div>
              <div className="project-wrapper__text">
                <Row>
                  <Col md={7}>
                    <p className="cta-btn color-text">{node.title}</p>
                  </Col>
                  <Col md={5}>
                    <p className="date">{node.date}</p>
                  </Col>
                </Row>
              </div>
            </div>
          </button>
          {/*  */}
          <Modal
            isOpen={modalIsOpen}
            style={modalStyle}
            onAfterOpen={() => (document.body.style.overflow = 'hidden')}
            onAfterClose={() => (document.body.style.overflow = 'auto')}
            onRequestClose={() => setIsOpen(false)}
          >
            <PostDetail node={node} />
          </Modal>
        </>
      )}
    </div>
  );
};

export default Post;
